import VueRouter from 'vue-router'
import Vue from 'vue'
import DesignNav from './components/DesignNav'
import FreeCommercial from './components/FreeCommercial'
import Home from './components/Home'
import Activity from './components/Activity'
import About from './components/About'

Vue.use(VueRouter);

export default new VueRouter({
    mode: 'history',
    routes: [
        { path: '/', component: FreeCommercial },
        { path: '/my-nav', component: Home },
        { path: '/design-nav', component: DesignNav },
        { path: '/free-commercial', component: FreeCommercial },
        { path: '/activity', component: Activity },
        { path: '/about', component: About },
    ],
    scrollBehavior (to, from, savedPosition) {
        //  # 
        // to.hash ()
        //  #3
        if (to.hash) {
            return {
                //  to.hash 
                //  html 
                //  #3  querySelector('#3') id = 3 
                selector: to.hash
            }
        }
    }
})


